<template>
  <div class="nav-bar">
      <ul>
        <router-link
          tag="li"
          v-for="nav in data"
          :key="nav.id"
          :to="{name: nav.url}">
          <img :src="nav.icon"><br>
          <span>{{nav.title}}</span>
        </router-link>
      </ul>
    </div>
</template>

<script>
export default {
  name: 'main_nav',
  data () {
    return {
      data: [
        {title: '首页', url: 'home', icon: '../static/img/主页.png'},
        {title: '分类', url: 'explorer', icon: '../static/img/分类.png'},
        {title: '购物车', url: 'cart', icon: '../static/img/购物车空.png'},
        {title: '我', url: 'me', icon: '../static/img/用户.png'}
      ]
    }
  }
}
</script>

<style lang='less'>
  .nav-bar{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 6.4rem;
    z-index: 1;
    li{
      display: inline-block;
      width: 1.6rem;
      height: .8rem;
      background: #ddd;
      font-weight: bold;
      text-align: center;
      transition: all .3s;
      cursor: pointer;
      &.router-link-exact-active.router-link-active {
        background: orange;
        color: #eee;
      }
      img {
        width: .4rem;
        margin-top: .1rem
      }
    }
  }
</style>



