<template>
  <div class="home">
    <slider
      :sliders="sliders"></slider>
    <fast-message
      :message="message"></fast-message>
    <book-list
      :heading="'最近更新'"
      :bookList="latestUpdate"
      @onBookSelect="preview"></book-list>
    <book-list
      :heading="'编辑推荐'"
      :bookList="recommended"
      @onBookSelect="preview"></book-list>
    <modal-dialog
      :ref="'dialog'"
      @dialogClose="selected = {}">
      <div slot="header">
        <div class="header">
          <img :src="selected.img_url">
          <table>
            <tr>
              <td>作者</td>
              <td>{{anthor(selected.anthor)}}</td>
            </tr>
            <tr>
              <td>页数</td>
              <td>{{selected.pageCount}}</td>
            </tr>
            <tr>
              <td>书号</td>
              <td>{{selected.id}}</td>
            </tr>
            <tr>
              <td>出版日期</td>
              <td>{{selected.publish}}</td>
            </tr>
          </table>
        </div>
      </div>
      <div class="details">
        <!-- 这里是内容插槽 -->
        <h1>{{selected.title}}</h1>
        <p>{{selected.description}}</p>
        <div class="price">
          <span>{{'￥' + selected.price * bookCount}}</span>
          <button
            @click="sub">-</button>
          <input type="text" v-model="bookCount">本
          <button
            @click="add">+</button>
        </div>
        <div class="buy">
          <button>立即购买</button><br>
          <button>加入购物车</button>
        </div>
      </div>
    </modal-dialog>
  </div>
</template>

<script>
import HomePageData from './home.json'
import Slider from './Slider'
import FastMessage from './FastMessage'
import BookList from '../components/BookList'
import ModalDialog from '../components/ModalDialog'

export default {
  name: 'home',
  components: {Slider, FastMessage, BookList, ModalDialog},
  data () {
    return {
      message: '',
      selected: {anthor: []},
      bookCount: 1,
      sliders: [],
      latestUpdate: [],
      recommended: []
    }
  },
  created () {
    for (let props in HomePageData) {
      this[props] = HomePageData[props]
    }
    console.log(HomePageData)
    // this.$http.get('../static/api/home.json').then(res => {
    //   for (let props in res.body) {
    //     this[props] = res.body[props]
    //   }
    // }, (error) => {
    //   console.log(`获取数据失败：${error}`)
    // })
  },
  methods: {
    preview (book) {
      this.selected = book
      this.$refs.dialog.open()
    },
    add () {
      this.bookCount++
    },
    sub () {
      this.bookCount--
      this.bookCount <= 1 ? this.bookCount = 1 : 1
    },
    anthor (an) {
      return an.join('，')
    }
  }
}
</script>

<style lang='less'>
  .home {
    .header {
      overflow: hidden;
      margin: .4rem 0 .2rem 0;
      padding: .2rem 0;
      border-top: 1px dashed #ccc;
      img {
        float: left;
        height: 2.5rem;
      }
      table {
        tr {
          height: .4rem;
          td:first-child {
            color: #999;
          }
        }
        td {
          padding: 0 .1rem;
        }
      }
    }
    .details {
      clear: both;
      h1 {
        padding: .1rem 0;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size: .25rem;
        text-align: center;
      }
      p {
        height: 1.8rem;
        padding: .1rem 0;
        overflow: auto;
        font-size: .22rem;
        line-height: .3rem;
        color: #999;
      }
      .price {
        padding: .2rem 0;
        border-top: 1px dashed #ddd;
        border-bottom: 1px dashed #ddd;
        text-align: right;
        span {
          float: left;
          color: #f00;
          font-size: 16px;
        }
        input {
          width: .4rem;
          height: .3rem;
          text-align: center;
          border: none;
        }
        button {
          width: .3rem;
          height: .3rem;
          background: transparent;
        }
      }
      .buy {
        text-align: center;
        button {
          width: 5rem;
          height: .6rem;
          margin: .2rem;
          font-size: .25rem;
          &:nth-child(1) {
            background: red;
            color: #ddd
          }
        }
      }
    }
    
  }
</style>



