<template>
  <section class="book-list">
    <header>
      <span>{{heading}}</span>
      <router-link
        :to="{name: 'explorer'}">更多 &gt;</router-link>
    </header>
    <ul>
      <li
        v-for="book in bookList"
        :key="book.id"
        @click="$emit('onBookSelect', book)">
        <img :src="book.img_url">
        <b class="title">{{book.title}}</b>
        <span class="anthor">{{book.anthor | anthor}}</span>
      </li>
    </ul>
  </section>
</template>

<script>
export default {
  name: 'book-list',
  props: ['heading', 'bookList'],
  filters: {
    anthor (param) {
      return param.join(',')
    }
  }
}
</script>

<style lang='less'>
  .book-list {
    margin-left: -.1rem;
    margin-bottom: .8rem;
    header {
      height: .8rem;
      line-height: .8rem;
      text-indent: .2rem;
      border-bottom: 1px dashed #ddd;
      span {float: left}
      a {float: right; margin-right: .2rem}
    }
    li {
      display: inline-block;
      width: 1.8rem;
      margin-left: .3rem;
      margin-bottom: .3rem;
      margin-top: .3rem;
      img {
        display: block;
        width: 1.8rem; // 3*1.8=5.4
      }
      .title {
        display: block;
        padding: .2rem 0;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .anthor {
        color: #999;
      }
    }
  }
</style>



